import React from 'react';
import {ExportOutlined,StepBackwardOutlined,PauseOutlined,StepForwardOutlined,CaretRightOutlined} from '@ant-design/icons';
import { Layout, Button } from "antd";
import './VideoShow.less'
import { AutoVis } from 'calliope-chart';
import formateTime from '@/tool/formateTime';
const { Footer, Content, Header } = Layout;
import TimeDisplay from  './TimeDisplay';
import VideoDisplay from  './VideoDisplay';
import TalkBottom from '../TalkBottomView/TalkBottom';
import TimeLine from '../TimeLine'
import 'react-timeline-bar/dist/index.css';
import * as d3 from 'd3';

var changeTheFact = true;

export default class VideoShow extends React.Component {
    constructor(props) {
        super(props);
        this.timer= [];
        this.needle = [];
    }


    //
    formerCardsLength = (index) => {
        let formerCardsLength = 0;
        for (let x=0;x<index;x++){
            formerCardsLength += 47.3 * this.props.cardItems[x].time/ 6000/this.props.sceneScale + 7.3
        }
        return formerCardsLength
    }

    //
    totalCardsLength = (index) => {
        let allCardsTotalLength = 0;
        for (let x=index;x<this.props.cardItems.length;x++){
            allCardsTotalLength += 47.3 * this.props.cardItems[x].time/ 6000/this.props.sceneScale + 7.3
        }
        allCardsTotalLength = allCardsTotalLength - 7.3
        return allCardsTotalLength
    }

    //
    totalCardsTime = (index) => {
        let allCardsTotalTime = 0;
        for (let x=index;x<this.props.cardItems.length;x++){
            allCardsTotalTime += this.props.cardItems[x].time
        }
        //this.props.changeAllVideoTime(allCardsTotalTime);
        return allCardsTotalTime;
    }

    toPercent(point){
        var str=Number(point*100).toFixed(1);
        str+="%";
        return str;
    }

    //
    needleMove = (allCardsTotalLength, allCardsTotalTime) => {
        for(let x=0; x< allCardsTotalTime; x=x+50){
            this.needle.push(setTimeout(() => {
                // let leftPosition = allCardsTotalLength-this.props.scenePosition
                let newPosition = this.props.scenePosition+allCardsTotalLength/(allCardsTotalTime/50)
                this.props.setPosition(newPosition);
                let percent1 = this.toPercent(newPosition/allCardsTotalLength)
                // let percent2 = this.toPercent(0.02+newPosition/allCardsTotalLength)
                var div = document.getElementById("timeMarks");
                div.setAttribute('style','background:linear-gradient(90deg,#f28b30 '+percent1+',rgb(246, 246, 247) '+percent1+');height:3px;width:100%;border-radius:15px;margin-top:10px;position: absolute;z-index: 0');
                if(this.props.nowVideoTime+50>=this.props.allVideoTime)
                {
                    this.props.changeNowVideoTime(this.props.allVideoTime)
                    if(document.getElementById("pause-btn"))
                        document.getElementById("pause-btn").click();
                }
                else
                {
                    this.props.changeNowVideoTime(this.props.nowVideoTime+50)
                }
        }, x));}
    }

    //
    clearAllTimeOut = () => {
        for (let i = 0; i < this.timer.length; i++) {
            clearTimeout(this.timer[i]);
        }
        for (let i = 0; i < this.needle.length; i++) {
            clearTimeout(this.needle[i]);
        }
    }

    //timer
    clearTimerTimeOut = () => {
        for (let i = 0; i < this.timer.length; i++) {
            clearTimeout(this.timer[i]);
        }
    }

    //needle
    clearNeedleTimeOut = () => {
        for (let i = 0; i < this.needle.length; i++) {
            clearTimeout(this.needle[i]);
        }
    }


    nextVideoChart = () => {
        if(changeTheFact == true){
        
            //
            if (this.props.videoIndex < this.props.cardItems.length - 1) {
                
                //
                if (this.props.isPlayVideo){
                    changeTheFact = false;
                    setTimeout(() => {
                        changeTheFact = true;
                    }, 4000);
                }

                //
                this.props.playVideoSlice(this.props.videoIndex + 1)

                //
                this.clearNeedleTimeOut();
                let needleForward = this.formerCardsLength(this.props.videoIndex+1)
                this.props.setPosition(needleForward);
                this.props.changeNowVideoTime(this.props.nowVideoTime+this.props.cardItems[this.props.videoIndex].time)

                //
                if (this.props.isPlayVideo) {
                    
                    //
                    this.needleMove(this.totalCardsLength(this.props.videoIndex+1),this.totalCardsTime(this.props.videoIndex+1));
                    
                    //
                    this.clearTimerTimeOut();

                    //
                    let allTime = this.props.cardItems[this.props.videoIndex+ 1].time

                    //
                    for (let i = 0; i < this.props.cardItems.length-this.props.videoIndex-2; i++) {
                        
                        this.timer.push(setTimeout(() => {

                            this.props.playVideoSlice(this.props.videoIndex + 1)

                            //,
                            if(this.props.videoIndex==this.props.cardItems.length-1)
                            {
                                //
                                setTimeout(()=>{
                                    this.props.stopPlayVideoContinue();
                                    this.clearNeedleTimeOut();
                                },this.props.cardItems[this.props.cardItems.length-1].time)

                            }
                            }, allTime));

                        allTime += this.props.cardItems[this.props.videoIndex+i+2].time;
                    }
                }
            }
        }
    }

    lastVideoChart = () => {
        if(changeTheFact == true){

            //
            if (this.props.videoIndex > 0) {

                //
                if (this.props.isPlayVideo){
                    changeTheFact = false;
                    setTimeout(() => {
                        changeTheFact = true;
                    }, 4000);
                }

                //
                this.props.playVideoSlice(this.props.videoIndex - 1)

                //
                this.clearNeedleTimeOut();
                let needleBack = this.formerCardsLength(this.props.videoIndex-1)
                this.props.setPosition(needleBack);
                this.props.changeNowVideoTime(this.props.nowVideoTime-this.props.cardItems[this.props.videoIndex-1].time)
             
                //
                if (this.props.isPlayVideo) {

                    //
                    this.needleMove(this.totalCardsLength(this.props.videoIndex-1),this.totalCardsTime(this.props.videoIndex-1));
                    
                    //
                    this.clearTimerTimeOut();

                    //
                    let allTime = this.props.cardItems[this.props.videoIndex-1].time

                    //
                    for (let i = 0; i < this.props.cardItems.length-this.props.videoIndex; i++) {
                        
                        this.timer.push(setTimeout(() => {

                            this.props.playVideoSlice(this.props.videoIndex + 1)

                            //,
                            if(this.props.videoIndex==this.props.cardItems.length-1)
                            {
                                //
                                setTimeout(()=>{
                                    this.props.stopPlayVideoContinue();
                                    this.clearNeedleTimeOut();
                                },this.props.cardItems[this.props.cardItems.length-1].time)

                            }
                            }, allTime));

                        allTime += this.props.cardItems[this.props.videoIndex+i].time;;
                    }
                }
            }
        }
    }

    exportMP4 = () => {
        console.log("export!")
    }

    playToPause = () => {
        
        //  -> 
            
            //
            /* if(this.props.videoIndex == this.props.cardItems.length-1)
                {
                    this.props.replayVideoSlice();
                    this.props.setPosition(0);
                    this.props.changeNowVideoTime(0)
                    return ;
                } */
            
            //
            this.props.playVideoContinue();
            this.props.changeNowVideoTime(0);

            //()
            /* let d = this.props.scenePosition - this.formerCardsLength(this.props.videoIndex);
            let t = d * this.props.sceneScale * 6000 / 47.3
            let allTime = this.props.cardItems[this.props.videoIndex].time - t; */

            //
            /* let allCardsTotalLength = this.totalCardsLength(this.props.videoIndex)-d;
            let allCardsTotalTime = this.totalCardsTime(this.props.videoIndex)- t; */
            
            //
            var allCardsTotalTime = this.props.allVideoTime
            var div = document.getElementById("timeline-ruler");
            var w = div.offsetWidth;
            let allCardsTotalLength = w
            this.needleMove(allCardsTotalLength,allCardsTotalTime);


            //index
            /* for (let i = 0; i < this.props.cardItems.length-this.props.videoIndex-1; i++) {
                
                this.timer.push(setTimeout(() => {

                    this.props.playVideoSlice(this.props.videoIndex + 1)
                    //,
                    if(this.props.videoIndex==this.props.cardItems.length-1)
                    {
                        //
                        setTimeout(()=>{
                            this.props.stopPlayVideoContinue();
                            this.clearNeedleTimeOut();
                        },this.props.cardItems[this.props.cardItems.length-1].time)

                    }
                    }, allTime));

                allTime += this.props.cardItems[this.props.videoIndex+i+1].time
            } */

    }

    //  -> 
    pauseToPlay = () => {
        //d3
        // title
        var videoChart = d3.select('.video-chart').select('svg').select('g')
        var title = videoChart.selectAll('text').selectAll('tspan')
        // circle
        var circleGroup =d3.select('.circleGroup')
        var circle = circleGroup.selectAll('circle')
        // tips
        var tipsGroup =d3.select('.content')
        var tips = tipsGroup.selectAll('text')
        // tooltips
        var toolGroup =d3.selectAll('.tooltip')


        title.transition().duration(0);
        circle.transition().duration(0);
        circle.transition("change color").duration(0);
        circle.transition("change layout").duration(0);
        circle.transition("change size").duration(0);
        tips.transition().duration(0);
        toolGroup.transition().duration(0);
        for(var i = 0; i < 9999; i++) {
            clearTimeout(i)
        }

        this.props.stopPlayVideoContinue();
        this.clearAllTimeOut();
        this.props.setPosition(0);
        /* var percent1 = '0%'
        var div = document.getElementById("timeMarks");
        if(div)
            div.setAttribute('style','background:linear-gradient(90deg,#f28b30 '+percent1+',rgb(246, 246, 247) '+percent1+');height:3px;width:100%;border-radius:15px;margin-top:10px;position: absolute;z-index: 0'); */
    }

    render() {
        return (
            <Content className='center-video-show'>
                <Layout className='center-layout'>
                    <Header className='center-header'>
                        
                        <div className="export">
                            <TalkBottom {...this.props}/>
                        </div>
                    </Header>
                    <VideoDisplay {...this.props} />
                    {<TimeLine {...this.props}/>}
                    {<Footer className='center-footer-player'>
                        <div className="left-empty"></div>
                        <div className="player-btn">
                            <div className='last-btn' >
                                {/* <StepBackwardOutlined style={{ fontSize: '18px' }} onClick={this.lastVideoChart} /> */}
                                <StepBackwardOutlined style={{ fontSize: '18px' }}/>
                            </div>
                            {this.props.isPlayVideo ?
                                <div className='pause-btn'><PauseOutlined id='pause-btn' style={{ fontSize: '18px' }} onClick={this.pauseToPlay} /></div>
                                :
                                <div className='play-btn'><CaretRightOutlined id='play-btn' style={{ fontSize: '18px' }} onClick={this.playToPause} /></div>
                            } 
                            <div className='next-btn'>
                                {/* <StepForwardOutlined style={{ fontSize: '18px' }} onClick={this.nextVideoChart} /> */}
                                <StepForwardOutlined style={{ fontSize: '18px' }}/>
                            </div>
                        </div>
                        <TimeDisplay {...this.props} />
                    </Footer>}
                </Layout>
            </Content>
        )
    }
}