import { connect } from 'react-redux';
import { cardItems,videoIndex,currentSpec,nowVideoTime,allVideoTime,isPlayVideo,scenePosition,sceneScale,narrative_spec,isGenerating,operatorType} from '@/selector/video';
import * as videoAction from '@/action/videoAction';
import VideoShow from './VideoShow.js';

const mapStateToProps = (state) => ({
    cardItems: cardItems(state),
    videoIndex:videoIndex(state),
    currentSpec:currentSpec(state),
    nowVideoTime:nowVideoTime(state),
    allVideoTime:allVideoTime(state),
    isPlayVideo:isPlayVideo(state),
    scenePosition: scenePosition(state),
    sceneScale: sceneScale(state),
    isGenerating : isGenerating(state),
    narrative_spec:narrative_spec(state),
    operatorType:operatorType(state),
})


const mapDispatchToProps = dispatch => {
    return {
        initialVideoSlice: (facts,schemaFilter,filename,datares,schema) => dispatch(videoAction.initialVideoSlice(facts,schemaFilter,filename,datares,schema)),
        replayVideoSlice:()=>dispatch(videoAction.replayVideoSlice()),
        playVideoSlice:(index)=>dispatch(videoAction.playVideoSlice(index)),
        playVideoContinue:()=>dispatch(videoAction.playVideoContinue()),
        stopPlayVideoContinue:()=>dispatch(videoAction.stopPlayVideoContinue()),
        addNowVideoTime:()=>dispatch(videoAction.addNowVideoTime()),
        setPosition: (position) => dispatch(videoAction.setPosition(position)),
        setSceneScale: (scale) => dispatch(videoAction.setSceneScale(scale)),
        changeNowVideoTime:(time)=>dispatch(videoAction.changeNowVideoTime(time)),
        changeAllVideoTime:(time)=>dispatch(videoAction.changeAllVideoTime(time)),
        isGeneratingGIF : (isGenerating) => dispatch(videoAction.isGeneratingGIF(isGenerating)),
        operatorChange : (operatorType) => dispatch(videoAction.operatorChange(operatorType)),
        animationChange : (animationType) => dispatch(videoAction.animationChange(animationType)),

        //video card
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(VideoShow);
