import React from 'react';
import { convertSizeToCssValue } from 'react-split-pane/lib/SplitPane';
import PerSeekBar from './PerSeekBar';

export default class TimeSeekBar extends React.Component {
    constructor(props) {
        super(props);
    }


    shouldComponentUpdate(nextProps, nextState) {
        if(this.props.barWidthList!=nextProps.barWidthList)
        {
            if(nextProps.barWidthList.length>=9)
            {
                nextProps.setSceneScale(2)
                return true;
            }
            else
            {
                nextProps.setSceneScale(1)
                return true;
            }
        }
        else
            return false;
    }

    //
    onMouseMove = (e) => {
        if (!this.props.isMouseDown || this.props.currentBarIndex < 0) return      
        const { dragDirection, currentX, leftOffsetWidth, rightOffsetWidth, currentBarIndex, setLeftBarWidth, setRightBarWidth } = this.props
        const offsetX = e.clientX - currentX;
        let distance=leftOffsetWidth+ offsetX
        console.log(leftOffsetWidth,leftOffsetWidth+ offsetX)

        var barScale = this.props.barWidthList.length>=9?2.03:1
        
        if(leftOffsetWidth+ offsetX<140/barScale){
            if(leftOffsetWidth===140/barScale)return
            setLeftBarWidth(currentBarIndex, 140)
            return
        }
        if (dragDirection === 'right') {
            setLeftBarWidth(currentBarIndex, distance*barScale)
        }
    }

    render() {
        const { barWidthList, cardItems ,dragCursor} = this.props
        return (
            <div className='time-seek-bar'>
                <div
                    className='all-seek-bar-slide'
                    onMouseMove={this.onMouseMove}
                    style={{cursor:dragCursor}}>
                    {
                        barWidthList.map((item, index) => {
                            return (
                                <PerSeekBar {...this.props} bar={item} index={index} key={index} time={cardItems.length === 0 ? 6000 : cardItems[index].time} />
                            )
                        })}

                </div>
            </div>
        )
    }
}
