import React from 'react';
import formateTime from '@/tool/formateTime.js'
export default class PerSeekBar extends React.Component {

    recordLocation = (e) => {
        this.props.setMouseDownState(true)
        this.props.setCurrentX(e.clientX)
        this.props.setLeftOffsetWidth(this.videotime.offsetWidth)
        // this.props.setRightOffsetWidth(this.drag.offsetWidth)
    }
    onMouseDownLeft = (e) => {
        console.log(this.props.index, 'mouseLeftDown')
        e.stopPropagation();
        this.props.setDragDirection('left')
        //index
        this.props.setCurrentBarIndex(this.props.index)
        this.recordLocation(e)
    }

    //
    onMouseDownRight = (e) => {
        e.stopPropagation();
        this.props.setDragDirection('right')
        //index
        this.props.setCurrentBarIndex(this.props.index)
        //TODO:id
        this.recordLocation(e)
        this.props.setDragCursor('ew-resize')
    }

    changeToDrag = (e) => {
        e.stopPropagation();
        let drag = this.props.bar.isDrag
        this.props.setDragState(this.props.index, !drag)
    }

    render() {
        const { bar, index, time } = this.props
        let num = index + 1 < 10 ? ("0" + (index + 1)) : ("" + (index + 1))
        var barScale = this.props.barWidthList.length>=9?2.03:1
        //
        return (
            <div className='per-seek-bar' >
                {
                    index !== 0 ? (
                        <div className='right-btn' style={{ width: '24px', minWidth: '24px' }}>
                            <div className='drag-icon'></div>
                        </div>) : null
                }

                {
                    bar.isDrag ?
                        (
                            <div
                                className='left-bar-drag'
                                ref={current => this.videotime = current}
                                style={
                                    {
                                        width: bar.leftbar/barScale,
                                        minWidth: 140/barScale,
                                        // position: dragDirection === 'left' && isMouseDown && currentBarIndex === index ? 'absolute' : 'relative',
                                        // left: dragDirection === 'left' && isMouseDown && currentBarIndex === index ? leftDist : '0px'
                                    }
                                }>
                                <div className='drag-line' onMouseDown={this.onMouseDownLeft}></div>
                                <div
                                    className='drag-time-text'                 
                                    style={{ width: (bar.leftbar - 13)/barScale, minWidth: 127/barScale }}
                                    onClick={this.changeToDrag}>
                                    {formateTime(time)}
                                </div>
                                <div className='drag-line-box' onMouseDown={this.onMouseDownRight}>
                                    <div className='drag-line' ></div>
                                </div>

                            </div>) :
                        (
                            <div
                                className='left-bar-undrag'
                                onClick={this.changeToDrag}
                                style={ this.props.cardItems[index].isGeneratedByAlg?{background:"#E4F2FB",width: bar.leftbar/barScale,minWidth: 140/barScale}:{background:"#FFE1A4",width: bar.leftbar/barScale,minWidth: 140/barScale}

                                 }>
                                {num}
                            </div>
                        )
                }
            </div >
        )
    }
}